export default {
  // 此处编写vue实例的配置项，通过一定语法可以混入组件内部
  // data method computed...
  // 但是同名组件内优先级高
  methods: {
    loginConfirm () {
      if (!this.$store.getters.token) {
        this.$dialog.confirm({
          title: '温馨提示',
          message: '此时需要登陆才能操作',
          confirmButtonText: '去登陆',
          cancelButtonText: '再逛逛'
        }).then(() => {
          // 如果希望跳转后能回来，需要携带路径地址
          // this.$route.fullpath(带参数)
          this.$router.replace({
            path: '/login',
            query: {
              backUrl: this.$route.fullPath
            }
          })
        }).catch(() => { })
        return true
      }
      return false
    }
  }
}
